/** 
 *fn   
 *  id : id
 * 
**/

import Modal from 'antd/lib/modal';import 'antd/lib/modal/style/css.js';
const { confirm } = Modal;
function showDeleteConfirm(fn:any,id:any) {
    confirm({
      title: '',
      okText: '',
      okType: 'danger',
      cancelText: '',
      content: '',
      centered:true,
      onOk() {
        if(typeof(fn)==="function"){
              fn(id)
            return 
        }
        console.warn('fn is not a function')
      },
      onCancel() {
        console.log('')
      },
    });
  }

export default showDeleteConfirm





