import * as React from "react";
import Button from 'antd/lib/button';import 'antd/lib/button/style/css.js';import Modal from 'antd/lib/modal';import 'antd/lib/modal/style/css.js';import Input from 'antd/lib/input';import 'antd/lib/input/style/css.js';import Form from 'antd/lib/form';import 'antd/lib/form/style/css.js';import Select from 'antd/lib/select';import 'antd/lib/select/style/css.js';import message from 'antd/lib/message';import 'antd/lib/message/style/css.js';
import { observer, inject } from 'mobx-react'
import { FormComponentProps } from "antd/lib/form";
import IsAffirm from '../../../components/dloag'
import "./css/grade.css";
interface UserFormProps extends FormComponentProps {
  age: number;
  name: string;
  history: any;
  user: any;
  form: any;
  classroom: any
  question: any
}
@inject('classroom', 'question')
@observer
class ClassRoom extends React.Component<UserFormProps, any> {
  state = {questiontypes: [],loading: false,visible: false,len: 0,mangergrade: [],mangerroom: [],subject: [],roomTitle: "",disableFlag: false,grade_name: '',room_text: '',subject_text: '',type: 1,obj: {}  };
  componentDidMount() {
    this.getMangerRooms()
  }
  getMangerRooms = async () => {
    const mangerroom = await this.props.classroom.getMangerRoom();
    const subject = await this.props.question.subject()
    const mangergrade = await this.props.classroom.getMangerGrade();
    this.setState({ mangerroom, subject, mangergrade });
  };

  //  
  addMangerGrade = async (str: Object) => {
    const mangerroom = await this.props.classroom.addMangerGrade(str);
    mangerroom == "" ? message.success(mangerroom) : message.warning(mangerroom);
    this.handleCancel()
    this.getMangerRooms()
  }

  handleOk = () => {
    this.setState({ loading: true });
  };

  //  
  showModal = () => {
    this.setState({ visible: true, roomTitle: "", disableFlag: false, grade_name: '', room_text: '', subject_text: '', type: 1, });
  };
  handleCancel = () => {
    this.setState({ visible: false });
  };

  UpdateMangerGrade = async (str: Object) => {
    const Updatemangergrade = await this.props.classroom.UpdateMangerGrade(str);
    message.success(Updatemangergrade)
    this.handleCancel()
    this.getMangerRooms()
  }

  //  && 
  handleSubmit = (e: any) => {
    const { type, obj } = this.state
    e.preventDefault();
    this.props.form.validateFields((err: any, values: any) => {
      if (!err) {
        if (type == 1) {
          this.addMangerGrade(values)
        } else {
          const newval = Object.assign(obj, values)
          // delete (newval.room_text)
          // delete (newval.subject_text)
          this.UpdateMangerGrade(newval)
        }
      }
    });
  };

  //   
  DelteMangerGrade = async (id: String) => {
    const result = await this.props.classroom.DelteMangerGrade({ grade_id: id })
    message.success(result)
    this.getMangerRooms()
  }
  // 
  removeRoom = (id: String) => {
    IsAffirm(this.DelteMangerGrade, id)
  }

  // 
  ChangeRoom = (item: any) => {
    this.setState({ visible: true, roomTitle: "", disableFlag: true, grade_name: item.grade_name, room_text: item.room_text, subject_text: item.subject_text, type: 2, obj: item })
  }

  public render() {
    const { visible, mangergrade, mangerroom, subject, roomTitle, disableFlag, grade_name, room_text, subject_text } = this.state;
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <div className="main">
        <h2 className="titType"></h2>
        <div className="typesContent">
          <div className="tableType">
            <div className="addtype">
              <Button type="primary" className="btnsa" onClick={this.showModal}>
                +
              </Button>
            </div>
            <table className="tablelist">
              <thead>
                <tr className="tabletr">
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {
                  mangergrade && mangergrade.map((el: any, index: any) => <tr key={index}>
                    <td>{el.grade_name}</td>
                    <td>{el.subject_text}</td>
                    <td>{el.room_text}</td>
                    <td className="tabletrDelte">
                      <span className="tabBtn" onClick={() => { this.ChangeRoom(el) }}></span> | <span className="tabBtn" onClick={() => { this.removeRoom(el.grade_id) }}></span>
                    </td>
                  </tr>)
                }
              </tbody>
            </table>
          </div>
        </div>
        <Modal visible={visible} title={roomTitle} onCancel={this.handleCancel} centered={true} destroyOnClose={true} keyboard footer={null}>
          <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>
            <Form.Item label="">
              {getFieldDecorator('grade_name', {
                initialValue: `${grade_name}`,
                rules: [{ required: true, message: "" }]
              })(<Input placeholder="" disabled={disableFlag} />)}
            </Form.Item>

            <Form.Item label="">
              {getFieldDecorator("room_id", {
                initialValue: `${room_text}`,
                rules: [{ required: true, message: "" }]
              })(
                <Select placeholder="" >
                  {mangerroom.map((Item: any) => <Option key={Item.room_id} value={Item.room_id}>{Item.room_text}</Option>)}
                </Select>
              )}
            </Form.Item>
            <Form.Item label="">
              {getFieldDecorator("subject_id", {
                initialValue: `${subject_text}`,
                rules: [{ required: true, message: "!" }]
              })(
                <Select placeholder="">
                  {subject.map((Item: any) => <Option key={Item.subject_id} value={Item.subject_id}>{Item.subject_text}</Option>)}
                </Select>
              )}
            </Form.Item>
            <Form.Item wrapperCol={{ span: 12, offset: 5 }}>
              <Button type="primary" htmlType="submit">
                
              </Button>
            </Form.Item>
          </Form>
        </Modal>
      </div>
    );
  }
}

export default Form.create()(ClassRoom);



